<template>
  <div>
    <van-nav-bar title="修改昵称" left-arrow left-text="返回" @click-left="$router.back()"></van-nav-bar>
    <van-cell-group>
      <van-field v-model="$store.user.nickname" clearable label="昵称" placeholder="请输入昵称"  />
      <van-button type="primary" size="large" @click="confirm">确认修改</van-button>
    </van-cell-group>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        nickname:''
      }
    },
    methods: {
      confirm(){
        this.$axios.post('/user/setting', {
          nickname:this.$store.user.nickname
        }).then(res => {
          if(res.errcode === 0){
            this.$router.back()
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


